.alignnone { 
    margin: 5px 20px 20px 0;
}

.aligncenter,
div.aligncenter { 
    display: block;
    margin: 5px auto 5px auto;
}

.alignright { 
    float:right;
    margin: 5px 0 20px 20px;
}

.alignleft { 
    float: left;
    margin: 5px 20px 20px 0;
}

a img.alignright { 
    float: right;
    margin: 5px 0 20px 20px;
}

a img.alignnone { 
    margin: 5px 20px 20px 0;
}

a img.alignleft { 
    float: left;
    margin: 5px 20px 20px 0;
}

a img.aligncenter { 
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.gallery-caption {
	display: inherit;
}

.sticky {
	display: inherit;
}
	
.entry-content { 
	overflow: hidden;
}

img { 
	max-width: 100%;
	height: auto;
}

.wp-caption { 
    background: $white;
    border: 1px solid $dark_gray;
    max-width: 96%; /* Image does not overflow the content area */
    padding: 5px 3px 10px;
    text-align: center;
}

.wp-caption.alignnone { 
    margin: 5px 20px 20px 0;
}

.wp-caption.alignleft { 
    margin: 5px 20px 20px 0;
}

.wp-caption.alignright { 
    margin: 5px 0 20px 20px;
}

.wp-caption img { 
    border: 0 none;
    height: auto;
    margin: 0;
    max-width: 98.5%;
    padding: 0;
    width: auto;
}

.wp-caption p.wp-caption-text { 
    font-size: 11px;
    line-height: 17px;
    margin: 0;
    padding: 0 4px 5px;
}

/* Text meant only for screen readers. */
.screen-reader-text { 
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;

	&:focus { 
		background-color: $light_gray;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		color: $blue;
		display: block;
		font-size: 14px;
		font-size: 0.875rem;
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000; /* Above WP toolbar. */
    }
}
